.rightBarFriend{
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    .rightbarProfileImgCont{
        margin-right: 10px;
        position: relative;
        .rightBarProfaileImg{
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
        }
        .rightbarOnline{
            width: 13px;
            height: 13px;
            border-radius: 50%;
            background-color: limegreen;
            position: absolute;
            top: -2px;
            right: 0;
            border: 1px solid white;
        }
    }
    .rigtbarUserName{
        font-weight: 500;
    }
}